

.footer {
  background-color: #dc143c ;
  color: rgb(207, 212, 223);
  text-align: center;
  padding: 60px 0px;
  font-size: 14px;
}

.footer_social {
  display: flex;
  gap: 20px;
  justify-content: center;
}

.footer_text  {
  font-size: 14px;
  color: rgb(255, 255, 255);
  margin-top: 20px;
  font-weight: bold;
}

/* Yükleniyor ekranı */
.loading-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #fff;
  z-index: 1000;
}

.loading-container .spinner {
  width: 50px;
  height: 50px;
  border: 6px solid #f4a261; /* Lokma rengi */
  border-top: 6px solid #2a9d8f; /* Dönen üst renk */
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

.loading-container p {
  margin-top: 15px;
  font-size: 18px;
  color: #555;
  text-align: center;
}

.loading-container img {
  margin-top: 15px;
  width: 100px;
  height: auto;
  border-radius: 50%;
}

